import React from 'react';
import style from './ToolBox.module.css';
import {ToolBoxItem} from './ToolsBoxItem';

const ToolBox=()=>{

        return(

            <div>

                {ToolBoxItem.map(

                    item=>(

                        <div  key={item.id}className={style.tool_box}>

                            <div className={style.software_title}>

                                <h3>{item.endesc}</h3>

                            </div>

                            <div className={style.software}>

                                {item.tools.map(

                                    tools=>(

                                        <div key={tools.id}>

                                            {tools.name}

                                        </div>

                                    )
                                )}

                            </div>
                            
                        </div>

                    )
                )}
                
            </div>
        )
}
export default ToolBox;