import React,{Component} from 'react';
import style from './Jumbotron.module.css';
import Button from './Button';
import {HashLink as Link} from 'react-router-hash-link';
import WeiSvg from './WeiSvg';




class Jumbotron extends Component{

    state={
        'title':"I'm Wei",
        'en_desc':"I'm a Multimedia Designer who has a great passion for UIUX, Motion graphics, and Visual Design. I help individuals and companies to achieve their goals through the ADDIE model and multimedia skills.",
        'zh_desc':"您好！我是張瑋晏，是一位多媒體設計師熱愛介面設計、動態圖像、視覺設計。我致力於幫助個人和企業透過多媒體和系統性的開發流程達成目標",
        'cta':"CHECK MY WORKS",
    }

    render(){
        const{title,en_desc,zh_desc,cta}=this.state;

        return(
            <div id="home" className={style.hero}>

                <div className={style.hero_text}>
                    
                    <h1>{title}</h1>

                    <div className={style.column}>
                        <p>{en_desc}</p>
                        <p>{zh_desc}</p>
                    </div>

                    <Link smooth to="/#portfolio">
                        <Button>{cta}</Button>
                    </Link>

                </div>

                <div className={style.hero_img}>
                    <WeiSvg/>
                </div>

            </div>
        )
    }
}
export default Jumbotron;